export default {
	name: 'cu-cell',
	props: {
		title: {
			type: String
		},
		label: {
			type: String
		},
		value: {
			type: String
		},
		isLink: {
			type: Boolean,
			value: false
		},
		url: {
			type: String,
			value: ''
		},
		params: {
			type: String,
			value: ''
		},
		isReturn: {
			type: Boolean,
			value: false
		},
		rName: {
			type: String,
			value: ''
		},
		rDatas: {
			type: Object,
			value: {}
		}
	},
	data() {
		return {
			isLastCell: true
		}
	},
	computed: {
		filterLabel() {
			return this.label.split('|')
		}
	},
	methods: {
		navigateTo () {
			let url = this.url
			if (this.params) {
				url += '?'+this.params
			}
			uni.navigateTo({
				url: url
			})
		},
		navigateReturn () {
			let pages =  getCurrentPages()
			let prevPage = pages[pages.length - 2]
			prevPage.setData({
				rName: this.rName,
				datas: this.rDatas
			})
			uni.navigateBack({
				delta: 1
			})
		},
		handleTap () {
			if (this.isLink) {
				this.navigateTo()
			}
			if (this.isReturn) {
				this.navigateReturn()
			}
		},
		updateIsLastCell (isLastCell) {
			this.isLastCell = isLastCell
		}
	}
}
